<template>
<div>
    <el-form>
  <el-input placeholder="请输入账号" v-model="username"></el-input>
  <el-input placeholder="请输入密码" v-model="password"></el-input>
  <el-input placeholder="请输入昵称" v-model="name"></el-input>
  <el-button @click="register">注册</el-button>
  </el-form>
</div>
</template>

<script>
export default {
    data(){
        return{
            username:'',
            password:'',
            name:''
        }
    },
    methods:{
        register(){
            this.$http.get('http://localhost:8181/addUser',{params:{
                username:this.username,
                password:this.password,
                name:this.name
            }}).then((res)=>{
                if(res.data==true){
                    this.$router.push('/login')
                }else{
                    alert('注册失败,用户名重复')
                }
            })
        }
    }
}
</script>

<style>

</style>


