<template>
  <div class="user-manager-container">
    <div class="one">
      <el-button
        type="primary"
        size="mini"
        @click="
          showAddUserDialog = true;
          edit = false;
        "
        >新增用户</el-button
      >
      <addUserDialog
        ref="addUserDialog"
        :edit="edit"
        v-model="showAddUserDialog"
        :row="row"
        :index="index"
        @addUser="addUser"
        @editData="editData"
      />

      <!-- <addUserDialog :value="showAddUserDialog" @input="input"   @addUser='addUser' />  -->

      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <el-button type="primary" icon="el-icon-search" @click="likeSearch" style="float: right; padding: 3px 0">搜索</el-button>
        </div>
        <div v-for="o in 1" :key="o" class="text item" style="display:flex;flex-direction:row;">
          <span>  姓名  <input    type='text' v-model="likename"> </span>
          <span>  身份证<input    type='text' v-model="likeidCard"></span>
          <span>  电话  <input    type='text' v-model="likephone"></span>
          <span>  生日  <input    type='text' v-model="likebirth"></span>
        </div>
      </el-card>

    </div>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column label="姓名" width="180">
        <template slot-scope="scope">
          <el-tag size="medium">{{ scope.row.name }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="出生日期" width="180">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.birthday }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="phone" label="联系方式" width="180">
      </el-table-column>
      <el-table-column prop="idCard" label="身份证号码" width="180">
      </el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <div class="block">
      <el-pagination
        layout="prev, pager, next"
        :total='total'
        :current-page="currentPage"
        @current-change="findSomeStudent"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>

import addUserDialog from './addUserDialog.vue'
export default {
  mounted() {
  this.$http.get('http://localhost:8181/findSomeStudent').then((res)=>{
    this.tableData=res.data
  })
},
  components:{
    addUserDialog
  },
  data() {
    return {
      tableData:[
        
      ],
      
      showAddUserDialog:false,
      edit:false,
      row:null,
      index:null,
      total: null,
      currentPage:1,
      
      likename:'',
      likeidCard:'',
      likephone:'',
      likebirth:'',

    };
  },
  created(){
    // this.$http.get("http://localhost:8181/findAllStudents").then((res)=>{
    //       this.total=res.data.length/3*10
    // })
    this.$http.get("http://localhost:8181/likeSearch",{params:{
        name:this.likename,
        phone:this.likephone,
        idCard:this.likeidCard,
        birthday:this.likebirth,
        // currentPage: this.currentPage
      }}).then((res)=>{
        console.log(res)
        this.total=res.data.length/3*10
      })
      // console.log(this.total)
  
  },
  
  methods: {


    handleEdit(index, row) {
      
      this.edit = true
      // 通过ref形式访问组件实例,可以调用该组件下的方法、属性
      // 避免代码冗余,混乱，与处理数据相关的逻辑都可以统一在setData中处理
      this.$refs.addUserDialog.setData(row,index)
      // this.row = row
      // this.index = index
      this.showAddUserDialog = true
      

    },
    handleDelete(index, row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.get('http://localhost:8181/delStudent',{params:{
            id:this.tableData[index].id,
          }})
          // 根据下标去删除用户
          this.tableData.splice(index, 1)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    // 添加用户
    addUser(data){
      // 这里涉及到一个知识点： JS的深拷贝
      this.tableData.push(JSON.parse(JSON.stringify(data)))
      // this.tableData.push(data)
    },
    editData(data){
      
      // this.tableData[data.index] = data.data
      this.$set(this.tableData,data.index,data.data)
    },
    findSomeStudent(currentPage,index){
    
    
    this.$http.get("http://localhost:8181/findSomeStudent",{params:{
      currentPage:currentPage
    }}).then((res)=>{
      this.tableData=res.data
    })
    },
   

    likeSearch(){
      
      this.$http.get("http://localhost:8181/likeSearch",{params:{
        name:this.likename,
        phone:this.likephone,
        idCard:this.likeidCard,
        birthday:this.likebirth,
        // currentPage: this.currentPage
      }}).then((res)=>{
        this.tableData=res.data
      })
      // console.log("curr  "+this.currentPage)

      // this.findSomeStudent(this.currentPage);
    },
    



    
  }
}
</script>

<style>
.text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
    
  }
 
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 100%;
    /* position: absolute; */
    /* right: 20px; */
  }
  .one{
    display: flex;
    flex-direction: row;
    }
</style>



