<template>
  <div class="top-nav-contaienr">
    <el-button @click="drawer = true" type="plain" style="margin-left: 16px;">
      用户：{{$store.state.userInfo.name}}
    </el-button>
    <div class="user-info">
      

      <el-drawer
        title="用户中心"
        :visible.sync="drawer"
        :direction="direction"
        :before-close="handleClose">
        <div style="margin-left:20px;">
            <span style="display:flex; flex-diraction:row;  border-bottom:1px solid black;">
            
            <el-upload
          class="avatar-uploader"
          action="https://jsonplaceholder.typicode.com/posts/"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
        

            <p>{{$store.state.userInfo.name}}</p>
            <span style="font-size:10px; color:grew; margin-top:50px;">系统原装个性签名，送给每一位用户</span>

            </span>

            <div>
              修改密码
            </div>
            <el-button 
            size="mini"
            type="primary"
            @click="loginout"
            style="position:absolute;bottom:20px;"
            >
            退出登录
            </el-button>

        </div>
        

      </el-drawer>


      

      
      
      </div>
    
  </div>
</template>

<script>
export default {

   data() {
      return {
        imageUrl: '',
        drawer: false,
        direction: 'rtl',
      };
    },
  methods:{
    loginout(){
      this.$store.dispatch("loginout",{}).then(res=>{
        this.$router.push('/login')
      })
    },
    handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
    }
  }

</script>

<style>
.user-info{
    display: flex;
    flex-direction: row;
    line-height: 40px;
}
/* .top-nav-contaienr{
display: flexbox;
flex-direction: row;

} */
.top-nav-contaienr {
  height: 80px;
  outline: 1px solid red;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

 .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 17px;
    height: 17px;
    line-height: 17px;
    text-align: center;
  }
  .avatar {
    width: 140px;
    height: 140px;
    display: block;
  }
</style>



