<template>
  <div>
    <el-dialog
      :title="edit ? '修改用户' : '新增用户'"
      :visible.sync="visible"
      width="50%"
      :before-close="handleClose"
    >
      <el-form
        :model="ruleForm"
        ref="form"
        :rules="rule"
        label-width="auto"
        size="mini"
      >
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="姓名" prop="name">
              <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="身份证号码" prop="idCard">
              <el-input v-model="ruleForm.idCard"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="联系方式" prop="phone">
              <el-input v-model="ruleForm.phone"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="出生日期" prop="birthday">
              {{ ruleForm.birthday }}

              <el-date-picker
                v-model="ruleForm.birthday"
                type="date"
                placeholder="选择日期"
                value-format="yyyy-MM-dd"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>



<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: "",
    },
    edit: {
      type: Boolean,
      default: false,
    },
    index: {
      type: Number,
      default: 0,
    },
    row: {
      type: Object,
      default: () => {},
    },
  },

  data() {
    // 在这里写关于表单规则的验证方法,也可以从外部引入方法（建议）
    // 身份验证
    function validatorIdCard(rule, value, callback) {
      const regIdCard = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if (rule.required && value === "") {
        callback("请输入身份证号");
      } else if (value === "") {
        callback();
      } else if (value && !regIdCard.test(value)) {
        return callback(new Error("请输入正确的身份证号码"));
      } else {
        callback();
      }
    }
    // 手机号验证
    function validatorPhone(rule, value, callback) {
      const regPhone =
        /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-79])|(?:5[0-35-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[189]))\d{8}$/;
      if (rule.required && value === "") {
        callback("请输入手机号码");
      } else if (value === "") {
        callback("");
      } else if (!regPhone.test(value)) {
        return callback(new Error("请输入正确的手机号码"));
      } else {
        callback();
      }
    }
    return {
      // 是否显示弹窗
      visible: false,
      // 表单提交字段
      ruleForm: {
        name: "",
        phone: "",
        birthday: "",
        idCard: "",
        test: "",
      },

      // 表单规则

      rule: {
        name: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        phone: [
          { required: false, validator: validatorPhone, trigger: "blur" },
        ],
        idCard: [
          { required: false, validator: validatorIdCard, trigger: "blur" },
        ],
      },
      editId: "",
    };
  },

  watch: {
    value(val) {
      if (val) {
        this.visible = val;
        // 后续可接着做初始化操作
      }
    },
  },

  methods: {
    submit() {
      // 提交按钮 进行表单验证
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          // 表单验证通过 进行接口提交
          // 这里有2个新知识点  async/await  和 es6 箭头函数
          // async/await B站黑马视频上有讲解自己看一下 es6的百度一下
          // api(data).then(res=>{
          //   // xxxxxx
          // })
          // let res = await api(data)
          if (this.edit) {
            // 修改逻辑
            this.$emit("editData", {
              data: JSON.parse(JSON.stringify(this.ruleForm)),

              index: this.editId,
            });

            this.$http
              .get("http://localhost:8181/updateStudent", {
                params: {
                  name: this.ruleForm.name,
                  idCard: this.ruleForm.idCard,
                  phone: this.ruleForm.phone,
                  birthday: this.ruleForm.birthday,
                  id:this.ruleForm.id,
                },
              }).then((res) => {});
              
          } else {
            //新增逻辑

            // 这里做假数据添加
            this.$emit("addUser", this.ruleForm);
            this.$http
              .get("http://localhost:8181/addStudent", {
                params: {
                  name: this.ruleForm.name,
                  idCard: this.ruleForm.idCard,
                  phone: this.ruleForm.phone,
                  birthday: this.ruleForm.birthday,
                },
              })
              .then((res) => {});
          }
          // 通过事件将数据抛出后,对表单进行重置操作并关闭弹窗
          this.handleClose();
        }
      });
    },
    // 关闭弹窗事件
    handleClose() {
      // 关于vue 双向绑定 v-model的语法糖写法
      // v-model 默认给子组件传参value,然后对应更新的方法,抛出一个事件 input
      this.visible = false;
      this.editId = "";
      this.$refs.form.resetFields();
      //$options.data是一个函数，返回结果是页面初始化时组件选项data中的数据。其返回结果有两个特点，
      //其一返回结果是一个静态对象，不随组件选项data的数据变化而变化。其二，返回结果只读，不能修改。
      this.ruleForm = this.$options.data().ruleForm;
      this.$emit("input", this.visible);
    },
    setData(data, index) {
      // 接收需要编辑的数据
      // es6写法 下行代码表示将 ruleForm对象和data对象 进行合并
      this.ruleForm = { ...this.ruleForm, ...data };
      this.editId = index;
      // this.ruleForm = data
    },
  },
};
</script>
<style>
</style>


