import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userInfo: {
      name: ''
    }
  },
  mutations: {
    SET_USERINFO: function (state, data) {
      state.userInfo = data
    }
  },
  actions: {
    setUserInfo: function ({ commit }, data) {
      commit('SET_USERINFO',data)
    },
    loginout:function({commit},data){
      return new Promise((resolve,reject)=>{
        commit('SET_USERINFO',data)
        resolve(true)
      })
    }
  }
})



