<template>
  <div class="login-container">
    <div class="left-panel">
      <div class="project-title">登录</div>
    </div>
    <div class="right-panel">
      <div class="login-title"><span class="title-span">账号登录</span></div>
      <div class="input-box">
        <img
          src="@/assets/login/icon-home.png"
          alt=""
        >
        <el-input
          v-model="userName"
          placeholder="请输入用户名"
        />
      </div>
      <div class="input-box">
        <img
          src="@/assets/login/icon-suo.png"
          alt=""
        >
        <el-input
          v-model="password"
          placeholder="请输入密码"
        />
      </div>
      <div
        class="login-button"
        @click="loginSubmit"
      >登录</div>
      <div
        class="register-button"
        @click="register"
      >注册</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userName: "",
      password: "",
    };
  },
  methods: {
    loginSubmit: function (params) {
      // 模拟一个假数据
      let userInfo = {
        name: "",
      };
      this.$http.get('http://localhost:8181/login',{params:{
        username:this.userName,
        password:this.password
      }}).then((res)=>{
        if(res.data==""){
          alert('用户名或密码错误')
        }else{
          userInfo.name=res.data.name
          this.$store.dispatch("setUserInfo", userInfo);
          // 路由跳转, 一种为path  一种为name 都可以
          this.$router.push({ name: "admin" });
        }
      })
    },
    register(){
      this.$router.push('/register')
    }
  },
};
</script>

<style >
.item {
  width: 50px;
  height: 50px;
  background: red;
}
.register-button{
  width: 480px;
  color: white;
  font-size: 24px;
  height: 70px;
  line-height: 70px;
  background: linear-gradient(to right, pink, yellow);
  border-radius: 50px;
  text-align: center;
  margin: 0 auto;
  margin-top: 10px;
  cursor: pointer;
}
.login-button {
  width: 480px;
  color: white;
  font-size: 24px;
  height: 70px;
  line-height: 70px;
  background: linear-gradient(to right, #3569fd, #abc1fe);
  border-radius: 50px;
  text-align: center;
  margin: 0 auto;
  margin-top: 120px;
  cursor: pointer;
}
.input-box {
  display: flex;
  align-items: center;
  height: 70px;
  width: 480px;
  border-radius: 50px;
  border: 1px solid #e9ebf6;
  margin: 0 auto;
  padding: 0 25px;
  box-sizing: border-box;
  margin-bottom: 20px;
}
.input-box > .el-input > .el-input__inner {
  border: none;
  font-size: 18px;
  height: 68px;
}
.title-span {
  position: relative;
  z-index: 99;
}
.login-title {
  color: #3569fd;
  font-size: 36px;
  text-align: center;
  position: relative;
  z-index: 10;
  margin-top: 135px;
  margin-bottom: 60px;
}
.login-title::after {
  content: "";
  height: 8px;
  width: 72px;
  background: #abc1fe;
  position: absolute;
  border-radius: 10px;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  z-index: 9;
}
.login-container {
  height: 800px;
  width: 1400px;
  display: flex;
  border-radius: 25px;

  /* overflow: hidden; */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.left-panel {
  position: relative;
  width: 665px;
  background: url("~@/assets/login/left-bg.png");
}
.project-title {
  font-size: 36px;
  color: white;
  position: absolute;
  left: 80px;
  top: 80px;
}
.right-panel {
  flex: 1;
  background: url("~@/assets/login/right-bg.png");
  box-shadow: 0 0 24px rgba(148, 154, 176, 0.5);
  border-radius: 20px;
}
</style>


