<template>
  <div class="layout-container">
    <navTop />
    <leftMenu />
    <div class="main-views">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
import navTop from './navTop.vue'
import leftMenu from './leftMenu.vue'

export default {
  components:{
    navTop,leftMenu
  }
}
</script>

<style>
.layout-container {
  margin-left: 200px;
}
.main-views {
  padding: 20px;
  box-sizing: border-box;
}
</style>


